import { Link } from 'gatsby'
import React from 'react'
import "../styles/nav.css"

export default function Nav() {
    return (
        <nav className='nav flex px-16 gap-4 just' >
            <h1 className='flex-1 my-auto'></h1>
            <ul className='items-center flex justify-start text-white' >
                <li className='mx-4  my-auto'><Link to="/">Home</Link></li>
                <li className='mx-4  my-auto'><Link  to="/blog">Blog</Link></li>
                <li className='mx-4  my-auto'><Link  to="/contact">Contact</Link></li>
                <li className='mx-4  my-auto'><Link  to="/about">About</Link></li>
                <li className='mx-4  my-auto border-2 rounded-lg radius-1 border-white p-2'><Link  to="/resume">Resume</Link></li>
            </ul>
        </nav>
    )
}
